import { Pipe, PipeTransform } from '@angular/core';
import moment from 'moment-es6';
import { chain, compact, sortBy, startCase, toLower } from 'lodash';
import { RX_RULE_DEFINITION } from './rule-definition.constant';
import { IIntervalTimeCriteria, IScheduleTimeCriteria } from './rule-definition.types';

@Pipe({
  name: 'rxRuleTriggerTimeCriteriaDisplayValue'
})
export class RxRuleTriggerTimeCriteriaDisplayValuePipe implements PipeTransform {
  transform(ruleTriggerTimeCriteria: IIntervalTimeCriteria | IScheduleTimeCriteria): string {
    let displayValue;

    switch (ruleTriggerTimeCriteria.resourceType) {
      case RX_RULE_DEFINITION.ruleElementResourceTypes.intervalTime:
        displayValue = this.buildIntervalTimeDisplayValue(ruleTriggerTimeCriteria as IIntervalTimeCriteria);
        break;
      case RX_RULE_DEFINITION.ruleElementResourceTypes.scheduleTime:
        displayValue = this.buildScheduleTimeDisplayValue(ruleTriggerTimeCriteria as IScheduleTimeCriteria);
        break;
      default:
        displayValue = '';
    }

    return displayValue;
  }

  private buildIntervalTimeDisplayValue(intervalTimeCriteria: IIntervalTimeCriteria): string {
    const displayValueParts = [];

    function buildDisplayValuePart(value: number, singularUnit: string, pluralUnit: string): string {
      return value ? `${value} ${value > 1 ? pluralUnit : singularUnit}` : '';
    }

    displayValueParts.push(buildDisplayValuePart(intervalTimeCriteria.days, 'day', 'days'));
    displayValueParts.push(buildDisplayValuePart(intervalTimeCriteria.hours, 'hour', 'hours'));
    displayValueParts.push(buildDisplayValuePart(intervalTimeCriteria.minutes, 'minute', 'minutes'));

    return `Every ${compact(displayValueParts).join(', ')}`;
  }

  private buildScheduleTimeDisplayValue(scheduleTimeCriteria: IScheduleTimeCriteria): string {
    const weekDayOrderIds = {
      Monday: 1,
      Tuesday: 2,
      Wednesday: 3,
      Thursday: 4,
      Friday: 5,
      Saturday: 6,
      Sunday: 7
    };

    function enumerateValues(values: (string | number)[]): string {
      const trailingValue = values.length > 1 ? ` and ${values.pop()}` : '';

      return `${values.join(', ')}${trailingValue}`;
    }

    const formattedWeekDays = chain(scheduleTimeCriteria.weekDays)
      .map(toLower)
      .map(startCase)
      .sortBy((weekDay) => weekDayOrderIds[weekDay])
      .value();

    const formattedTime = sortBy(scheduleTimeCriteria.hours).map((hour) =>
      moment(`${hour}:${scheduleTimeCriteria.minute}`, ['H:mm'], 'en').format('h:mmA')
    );

    const monthDaysPart = scheduleTimeCriteria.monthDays?.length
      ? `On dates ${enumerateValues(sortBy(scheduleTimeCriteria.monthDays))} of every month`
      : '';

    const weekDaysPart = scheduleTimeCriteria.weekDays?.length
      ? `${monthDaysPart ? ', and every ' : 'Every '}${enumerateValues(formattedWeekDays)}`
      : '';

    const timePart = ` at ${enumerateValues(formattedTime)}`;

    return `${monthDaysPart}${weekDaysPart}${timePart}`;
  }
}
