import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { ICommand, RxCommandFactoryService } from '@helix/platform/shared/api';
import { RxGuidService } from '@helix/platform/utils';
import { IRuleDefinition } from './rule-definition.types';

export const renameRuleDefinitionCommand = 'com.bmc.arsys.rx.application.rule.command.RenameRuleDefinitionCommand';
export const revertRuleCustomizationResourceType =
  'com.bmc.arsys.rx.application.rule.command.RevertRuleDefinitionCommand';

@Injectable({
  providedIn: 'root'
})
export class RxRuleDefinitionService {
  private renameCommand: ICommand;
  private revertCustomizationCommand: ICommand;

  constructor(
    private httpClient: HttpClient,
    private rxGuidService: RxGuidService,
    private rxCommandFactoryService: RxCommandFactoryService
  ) {
    this.renameCommand = rxCommandFactoryService.forResourceType(renameRuleDefinitionCommand);
    this.revertCustomizationCommand = rxCommandFactoryService.forResourceType(revertRuleCustomizationResourceType);
  }

  get(ruleDefinitionName: string): Observable<IRuleDefinition> {
    return this.httpClient.get<IRuleDefinition>(this.getUrl(ruleDefinitionName));
  }

  rename(oldRuleDefinitionName: string, newRuleDefinitionName: string): Observable<any> {
    return this.renameCommand.execute({
      name: oldRuleDefinitionName,
      newName: newRuleDefinitionName
    });
  }

  revertCustomization(ruleDefinitionName: string): Observable<any> {
    return this.revertCustomizationCommand.execute({ ruleDefinitionName });
  }

  update(ruleDefinition: IRuleDefinition): Observable<any> {
    return this.httpClient.put(this.getUrl(ruleDefinition.name), ruleDefinition);
  }

  private getUrl(ruleDefinitionName: string): string {
    return `/api/rx/application/rule/ruledefinition/${encodeURIComponent(ruleDefinitionName)}`;
  }
}
